import calendar from './calendar.png'

function Header() {
    return (
        <div className="m-5 mt-8 mx-auto flex justify-center">
            <div><img src={calendar} alt="calendar" className="h-10 w-10" /></div>
            <div><h1 className="page-title header-text text-white text-2xl text-center">&nbsp;a calendar</h1></div>
        </div>
    )
}

export default Header